/* style.css */

/* Smooth scrolling for anchor links */
html {
    scroll-behavior: smooth;
}

/* Hero Background - A modern gradient from Blue to Indigo */
.hero-bg {
    background: linear-gradient(135deg, #2563eb 0%, #4f46e5 100%);
    /* Optional: Add a subtle pattern overlay if you want more texture */
    background-image: linear-gradient(135deg, #2563eb 0%, #4f46e5 100%); 
}

/* Card Hover Effect */
.card-hover-effect {
    transition: all 0.3s ease-in-out;
    border: 1px solid transparent;
}

.card-hover-effect:hover {
    transform: translateY(-10px); /* Lifts the card up */
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    border-color: #bfdbfe; /* Light blue border on hover */
}

/* Custom Font Family adjustment if needed */
.font-family {
    font-family: 'Inter', sans-serif;
}